import { Component, Input, ChangeDetectionStrategy } from '@angular/core';

import { List } from 'immutable';

import { AbstractListFieldComponent } from '../abstract-list-field';

import { AppGlobalsService, JsonStoreService, TabIndexService, PathUtilService } from '../shared/services';


// FIXME: this doesn't have all stuff of AbstractListFieldComponent. Maybe, it shouldn't extend it.
export class PrimitiveListFieldComponent extends AbstractListFieldComponent {
   values: List<any>;
   schema: Object;
   path: Array<any>;

  constructor(public appGlobalsService: AppGlobalsService,
    public jsonStoreService: JsonStoreService,
    public tabIndexService: TabIndexService,
    public pathUtilService: PathUtilService) {
    super(appGlobalsService, jsonStoreService, tabIndexService, pathUtilService);
  }

  /**
   * Returns path of an element at index.
   * @override
   */
  getValuePath(index: number): Array<any> {
    return this.path.concat(index);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'primitive-list-field',
  styles: ["td { padding: 0px !important; } "],
  template: "<div [id]=\"pathString\"> <div class=\"wide\"> <table class=\"table\"> <tr *ngFor=\"let value of values | selfOrEmpty:schema; let i = index; trackBy:trackByFunction\"> <td> <primitive-field [value]=\"value\" [schema]=\"schema.items\" [path]=\"getValuePath(i)\"></primitive-field> </td> <!-- UP/DOWN and DELETE buttons for each row --> <td *ngIf=\"values.size > 0\" class=\"button-holder\"> <button type=\"button\" class=\"editor-btn-delete\" (click)=\"deleteElement(i)\"> &times; </button> <button *ngIf=\"i > 0\" type=\"button\" class=\"editor-btn-move-up\" (click)=\"moveElement(i, -1)\"> <i class=\"fa fa-chevron-up\" aria-hidden=\"true\"></i> </button> <button *ngIf=\"i < (values.size - 1)\" type=\"button\" class=\"editor-btn-move-down\" (click)=\"moveElement(i, 1)\"> <i class=\"fa fa-chevron-down\" aria-hidden=\"true\"></i> </button> </td> </tr> </table> </div> </div> ",
  changeDetection: ChangeDetectionStrategy.OnPush
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: AppGlobalsService, },
{type: JsonStoreService, },
{type: TabIndexService, },
{type: PathUtilService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'values': [{ type: Input },],
'schema': [{ type: Input },],
'path': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



